            <div data-v-36d08a7e="" id="betrule" class="agree_zz" style="display: none;">
                <div data-v-36d08a7e="" class="wrapper">
                    <p data-v-36d08a7e="" class="agree_title">Privacy Policy</p>
                    <div data-v-36d08a7e="" class="content_agree">
                        <div data-v-36d08a7e="" class="contents">
                            <div data-v-36d08a7e="" data-v-5197ff2a="">
                                <p data-v-36d08a7e="" data-v-5197ff2a="">In order to protect the legitimate rights and
                                    interests of users participating in the presale and
                                    maintain the
                                    normal operation order of the presale, the rules are formulated in accordance with
                                    relevant agreements and
                                    rules
                                    of national laws and regulations.</p>
                                <p data-v-36d08a7e="" data-v-5197ff2a=""><span data-v-36d08a7e="" data-v-5197ff2a=""
                                        style="color: rgb(0, 150, 136); font-size: 14px;">Chapter 1 Definition</span>
                                </p>
                                <p data-v-36d08a7e="" data-v-5197ff2a="" class="MsoListParagraph"
                                    style="margin-left: 8px;">1.1<span data-v-36d08a7e="" data-v-5197ff2a=""
                                        style="font-variant-numeric: normal; font-variant-east-asian: normal; font-stretch: normal; font-size: 9px; line-height: normal;">&nbsp;&nbsp;
                                    </span>Presale definition: refers to a sales model in which a merchant provides a
                                    product or service plan,
                                    gathers consumer orders through presale product tools, and provides goods and / or
                                    services to consumers
                                    according to prior agreement.</p>
                                <p data-v-36d08a7e="" data-v-5197ff2a="" class="MsoListParagraph"
                                    style="margin-left: 8px;">1.2<span data-v-36d08a7e="" data-v-5197ff2a=""
                                        style="font-variant-numeric: normal; font-variant-east-asian: normal; font-stretch: normal; font-size: 9px; line-height: normal;">&nbsp;&nbsp;
                                    </span>The presale model is a "deposit" model. "Deposit" refers to a fixed amount of
                                    presale commodity price
                                    pre-delivered. The deposit can participate in small games and have the opportunity
                                    to win more deposits.
                                    The
                                    deposit can be directly exchanged for commodities. The deposit is not redeemable.
                                </p>
                                <p data-v-36d08a7e="" data-v-5197ff2a="" class="MsoListParagraph"
                                    style="margin-left: 8px;">1.3<span data-v-36d08a7e="" data-v-5197ff2a=""
                                        style="font-variant-numeric: normal; font-variant-east-asian: normal; font-stretch: normal; font-size: 9px; line-height: normal;">&nbsp;&nbsp;
                                    </span>Presale products: refers to the products released by merchants using presale
                                    product tools. Only the
                                    presale words are marked on the product title or product details page, and the
                                    products that do not use
                                    the
                                    presale product tools are not presale products.</p>
                                <p data-v-36d08a7e="" data-v-5197ff2a="" class="MsoListParagraph"
                                    style="margin-left: 8px;">1.4<span data-v-36d08a7e="" data-v-5197ff2a=""
                                        style="font-variant-numeric: normal; font-variant-east-asian: normal; font-stretch: normal; font-size: 9px; line-height: normal;">&nbsp;&nbsp;
                                    </span>Presale system: Refers to the system product tools provided to support
                                    merchants for presale model sales.
                                </p>
                                <p data-v-36d08a7e="" data-v-5197ff2a="" class="MsoListParagraph"
                                    style="margin-left: 8px;">1.5<span data-v-36d08a7e="" data-v-5197ff2a=""
                                        style="font-variant-numeric: normal; font-variant-east-asian: normal; font-stretch: normal; font-size: 9px; line-height: normal;">&nbsp;&nbsp;
                                    </span>Presale commodity price: refers to the selling price of presale commodity.
                                    The price of presale goods is
                                    composed of two parts: deposit and final payment.</p>
                                <p data-v-36d08a7e="" data-v-5197ff2a="" class="MsoListParagraph"
                                    style="margin-left: 8px;">1.6<span data-v-36d08a7e="" data-v-5197ff2a=""
                                        style="font-variant-numeric: normal; font-variant-east-asian: normal; font-stretch: normal; font-size: 9px; line-height: normal;">&nbsp;&nbsp;
                                    </span>Presale deposit: Refers to a certain amount of money that consumers pay in
                                    advance when purchasing
                                    presale goods, which is mainly used as a guarantee to purchase presale goods and
                                    determine the purchase
                                    quota.
                                </p>
                                <p data-v-36d08a7e="" data-v-5197ff2a="" class="MsoListParagraph"
                                    style="margin-left: 8px;">1.7<span data-v-36d08a7e="" data-v-5197ff2a=""
                                        style="font-variant-numeric: normal; font-variant-east-asian: normal; font-stretch: normal; font-size: 9px; line-height: normal;">&nbsp;&nbsp;
                                    </span>Presale final payment: refers to the amount of money that the consumer still
                                    has to pay after the presale
                                    commodity price minus the deposit.</p>
                                <p data-v-36d08a7e="" data-v-5197ff2a="" class="MsoListParagraph"
                                    style="margin-left: 8px; text-indent: 0px;">&nbsp;</p>
                                <p data-v-36d08a7e="" data-v-5197ff2a=""><span data-v-36d08a7e="" data-v-5197ff2a=""
                                        style="color: rgb(0, 150, 136); font-size: 14px;">Chapter 2 Presale management
                                        specifications</span></p>
                                <p data-v-36d08a7e="" data-v-5197ff2a=""><span data-v-36d08a7e="" data-v-5197ff2a=""
                                        style="color: rgb(0, 150, 136); font-size: 13px;">2.1 Commodity
                                        management</span></p>
                                <p data-v-36d08a7e="" data-v-5197ff2a="">2.1.1 Presale deposit time: up to 7 days can be
                                    set.</p>
                                <p data-v-36d08a7e="" data-v-5197ff2a="">2.1.2 Presale final payment time: The start
                                    time of the final payment is within 7 days.</p>
                                <p data-v-36d08a7e="" data-v-5197ff2a="">2.1.3 During the presale of commodities, the
                                    system does not support merchants to modify the price of
                                    pre-sold
                                    commodities (including deposits and balances), but the amount of remaining commodity
                                    inventory can be
                                    modified
                                    according to the actual situation of inventory.</p>
                                <p data-v-36d08a7e="" data-v-5197ff2a="">2.1.4 To avoid unnecessary disputes, If the
                                    presale product is a customized product, the merchant should
                                    clearly
                                    inform the consumer on the product page of the production cycle and delivery time of
                                    the product, and
                                    contact
                                    the consumer to confirm the delivery standard, delivery time, etc.</p>
                                <p data-v-36d08a7e="" data-v-5197ff2a="">2.1.5 For customized products, the merchant has
                                    not agreed with the consumer on the delivery time and
                                    delivery
                                    standard, the delivery standard proposed by the consumer is unclear or conflicts and
                                    after the merchant
                                    places
                                    an order, he(she) starts production and delivery without confirming with the
                                    consumer, if the consumer
                                    initiates
                                    a dispute as a result, the platform will treat it as a normal delivery time limit
                                    order fulfillment.</p>
                                <p data-v-36d08a7e="" data-v-5197ff2a=""><span data-v-36d08a7e="" data-v-5197ff2a=""
                                        style="color: rgb(0, 150, 136); font-size: 13px;">2.2 Transaction
                                        management</span></p>
                                <p data-v-36d08a7e="" data-v-5197ff2a="">2.2.1 Consumers who use the pre-sale system
                                    will lock in the pre-sale quota after placing an order for
                                    goods. If
                                    the pre-sale order is overtime, the system will automatically cancel it.</p>
                                <p data-v-36d08a7e="" data-v-5197ff2a="">2.2.2 During the presale period, the merchant
                                    shall not cancel the presale activities without reason. For
                                    presale
                                    activities that have generated orders, the merchant must not cancel the order
                                    without the consumer s
                                    consent.
                                    If the consumer agrees, the merchant should double return the deposit paid by the
                                    consumer; if the
                                    consumer does
                                    not agree to cancel the order, the merchant should perform the contract according to
                                    the order.</p>
                                <p data-v-36d08a7e="" data-v-5197ff2a="">2.2.3 If the final payment of the presale order
                                    is not completed due to consumer reasons, the merchant
                                    can deduct
                                    the deposit paid by the consumer; if the merchant actively negotiates with the
                                    consumer to terminate the
                                    order
                                    before paying the final payment, the merchant shall double Return the deposit paid
                                    by the consumer.</p>
                                <p data-v-36d08a7e="" data-v-5197ff2a=""><span data-v-36d08a7e="" data-v-5197ff2a=""
                                        style="color: rgb(0, 150, 136); font-size: 13px;">2.3 Delivery Management</span>
                                </p>
                                <p data-v-36d08a7e="" data-v-5197ff2a="">2.3.1 Delivery time limit setting</p>
                                <p data-v-36d08a7e="" data-v-5197ff2a="">If the merchant sets the delivery time limit
                                    through the presale system, it should be shipped within the
                                    set time
                                    limit.</p>
                                <p data-v-36d08a7e="" data-v-5197ff2a="">2.3.2 Shipping way</p>
                                <p data-v-36d08a7e="" data-v-5197ff2a="">The third-party delivery the orders.</p>
                                <p data-v-36d08a7e="" data-v-5197ff2a="">Customers need to provide your name, address
                                    and phone number to facilitate third-party delivery
                                    orders.</p>
                                <p data-v-36d08a7e="" data-v-5197ff2a=""><span data-v-36d08a7e="" data-v-5197ff2a=""
                                        style="color: rgb(0, 150, 136); font-size: 13px;">2.4 After-sales
                                        management</span></p>
                                <p data-v-36d08a7e="" data-v-5197ff2a="">Presale products shall provide after-sales
                                    service in accordance with the "Regulations for After-sales
                                    Service of
                                    Platform Merchants".</p>
                                <p data-v-36d08a7e="" data-v-5197ff2a="">&nbsp;</p>
                                <p data-v-36d08a7e="" data-v-5197ff2a=""><strong data-v-36d08a7e=""
                                        data-v-5197ff2a="">Explanation</strong></p>
                                <p data-v-36d08a7e="" data-v-5197ff2a="">Mall transaction mode</p>
                                <p data-v-36d08a7e="" data-v-5197ff2a="">There are two ways to buy in the mall, one is
                                    full purchase, and the other is deposit purchase.</p>
                                <p data-v-36d08a7e="" data-v-5197ff2a="">In the mode of full purchase, you can place an
                                    order directly and purchase goods in full payment.</p>
                                <p data-v-36d08a7e="" data-v-5197ff2a="">The deposit purchase mode will freeze the
                                    customer's deposit, and the customer will take delivery after
                                    completing the final payment within 7 days. Users who have paid a deposit will be
                                    given an extra point
                                    quiz
                                    game. According to the analysis of market fluctuations, they have the opportunity to
                                    win more points to
                                    deduct
                                    the payment or send red envelopes to friends.</p>
                                <p data-v-36d08a7e="" data-v-5197ff2a="">&nbsp;</p>
                                <p data-v-36d08a7e="" data-v-5197ff2a=""><a data-v-36d08a7e=""
                                        data-v-5197ff2a=""></a>The pre-order model has many benefits for customers. The
                                    deposit not only generates an order for
                                    the
                                    customer, but also gives the customer an equal amount of red envelopes, which can be
                                    withdrawn
                                    immediately.
                                    Although the deposit is not refundable, the red envelope converted from the deposit
                                    can be withdrawn
                                    without any
                                    loss to the customer.</p>
                                <p data-v-36d08a7e="" data-v-5197ff2a="">&nbsp;</p>
                                <p data-v-36d08a7e="" data-v-5197ff2a="">. After the customer pays the deposit and
                                    orders, a merchandise order is generated, and the mall began
                                    to
                                    prepare this order. This deposit cannot be returned. After the customer needs to
                                    make up the balance, the
                                    mall
                                    will ship the goods. If the customer does not make up the balance, the product order
                                    will always
                                    exist.</p>
                                <p data-v-36d08a7e="" data-v-5197ff2a="">. After paying the deposit, the customer will
                                    be given a red envelope account with the same amount of
                                    deposit.
                                </p>
                                <p data-v-36d08a7e="" data-v-5197ff2a="">1. The red envelope can be withdrawn directly,
                                    so that instead of losing money, the customer has added a
                                    commodity order generated by a deposit. And red envelopes can also be given to
                                    friends.</p>
                                <p data-v-36d08a7e="" data-v-5197ff2a=""><span data-v-36d08a7e="" data-v-5197ff2a=""
                                        style="font-size: 14px;">2. If the customer uses the red envelope account to
                                        participate in
                                        the game, you can earn more red envelopes, but if the game loses, the red
                                        envelope will be gone, but his
                                        merchandise order is still there.</span></p>
                                <p data-v-36d08a7e="" data-v-5197ff2a=""><br data-v-36d08a7e="" data-v-5197ff2a=""></p>
                                <p data-v-36d08a7e="" data-v-5197ff2a="">Note: I have carefully read all contents of
                                    this presale management rule, Risk Disclosure Agreement and
                                    Risk
                                    Agreement and I am agreed to continue with my own risk.</p>
                            </div>
                        </div>
                    </div>
                    <div data-v-36d08a7e="" class="close_btn" style="border-top: 1px solid rgb(224, 224, 224);"><button
                            @click="hidebetrule()" data-v-36d08a7e="">CLOSE</button></div>
                </div>
            </div>
        </div>
        <div id="snackbar" class="van-toast van-toast--middle van-toast--text" style="z-index: 2009;display:none ">
            <div class="van-toast__text">success</div>
        </div>
        <div data-v-74fec56a="" data-v-b74b9dc6="" id="loading" class="loading" style="display: none;">
            <div data-v-74fec56a="" class="v-dialog v-dialog--persistent" style="width: 300px; display: block;">
                <div data-v-74fec56a="" data-v-5197ff2a="" class="v-card v-sheet theme--dark teal">
                    <div data-v-74fec56a="" data-v-5197ff2a="" class="v-card__text"><span
                            data-v-74fec56a="">Loading</span>
                        <div data-v-74fec56a="" data-v-5197ff2a="" role="progressbar" aria-valuemin="0"
                            aria-valuemax="100" class="v-progress-linear mb-0" style="height: 7px;">
                            <div data-v-74fec56a="" class="v-progress-linear__background white"
                                style="height: 7px; opacity: 0.3; width: 100%;"></div>
                            <div data-v-74fec56a="" class="v-progress-linear__bar">
                                <div data-v-74fec56a=""
                                    class="v-progress-linear__bar__indeterminate v-progress-linear__bar__indeterminate--active">
                                    <div data-v-74fec56a="" class="v-progress-linear__bar__indeterminate long white">
                                    </div>
                                    <div data-v-74fec56a="" class="v-progress-linear__bar__indeterminate short white">
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import axios from "axios";
export default {
    name: "WinView",
  props: ['rowsrec'],
    data() {
        return {
            isActive: false,
            userdetails: {
                username: null,
                balance: null,
                period: null,

            },
            betbox: {
                title: null,
                target: null,
                boxcolor: null,
                timeout: false,
                multiply: 1,
                factor: 10,
                amount: 10,
                page: 1,
                page1: 1,
                total: 1000,
                total1: null,

            },
            resultrec: '',
            info: '',
            betrec: ''

        }

    },
    mounted: function () {
        this.countdown();
        this.loading();
        this.refreshcountdown = setInterval(this.countdown, 1000);
        this.expand();
    },
    beforeUnmount: function () {
        console.log("unmount");
        clearInterval(this.refreshcountdown);

    },
    created: function () {
        this.fechresultrec();
        // this.expand();
    },
    methods: {
        pop(message) {
            document.getElementById("snackbar").innerHTML = message;
            document.getElementById("snackbar").style.display = "";
            setTimeout(function () { document.getElementById("snackbar").style.display = "none"; }, 1000);
        },
        fechresultrec() {
            axios.get('https://demo.com/trova/src/api/bet.php?action=resultrec&page=' + this.betbox.page).then((res) => {
                this.resultrec = res.data;
                console.log(this.resultrec);
            }).catch((err) => {
                console.log(err);
            })
            this.userdetails.username = localStorage.getItem('username');
            axios.get('https://demo.com/trova/src/api/bet.php?action=betrec&user=' + this.userdetails.username + '&page1=' + this.betbox.page1).then((res) => {
                this.betrec = res.data;
                console.log(this.betrec);
            }).catch((err) => {
                console.log(err);
            })

            axios.get('https://demo.com/trova/src/api/bet.php?action=info&user=' + this.userdetails.username).then((res) => {
                this.info = res.data;
                this.betbox.total = this.info[1].total;
                this.betbox.total1 = this.info[3].total1;
                this.userdetails.balance = this.info[0].balance;
                this.userdetails.period = this.info[2].period;
                if (this.betbox.total1 > 0) {
                    document.getElementById("norecord").style.display = "none";
                }
                console.log(this.info);
            }).catch((err) => {
                console.log(err);
            })
        },
        betamount(x,y,z,a){
            var amount;
            var f;
            if(z=="fail"){
                f=95/100*x;
                amount="-" + f.toFixed(2) ;
                return amount;
            }else if(z=='success'){
                if(a=="violet"){
                    if(y=="green" || y=="red"){
                    f=95/100*x*1.5;
                    amount="+" + f.toFixed(2) ;
                    return amount;
                }else if(y=="violet"){
                    f=95/100*x*3;
                    amount="+" + f.toFixed(2) ;
                    return amount;
                         }else{
                            f=95/100*x*9;
                    amount="+" + f.toFixed(2) ;
                    return amount;
                         }
                }else{
                    if(y=="green" || y=="red"){
                        f=95/100*x*2;
                    amount="+" + f.toFixed(2) ;
                    return amount;
                }else if(y=="violet"){
                    f=95/100*x*1.5;
                    amount="+" + f.toFixed(2) ;
                    return amount;
                         }else{
                            f=95/100*x*9;
                    amount="+" + f.toFixed(2) ;
                    return amount;
                         }
                }
               
            }else{
                return "";
            }
        },
        loading() {
            document.getElementById("load").style.display = "";
            document.getElementById("content").style.display = "none";
            document.getElementById("recordbox").style.display = "none";
            setTimeout(function () {
                document.getElementById("load").style.display = "none";
                document.getElementById("content").style.display = "";
                document.getElementById("recordbox").style.display = "";
            }, 300);

            document.getElementById("loading").style.display = "";
            setTimeout(function () { document.getElementById("loading").style.display = "none"; }, 200);

        },
        showbetrule() {
            document.getElementById("betrule").style.display = "";
        },
        hidebetrule() {
            document.getElementById("betrule").style.display = "none";
        },
        button(label) {
            console.log(label);
            if (this.betbox.timeout) {
                if (label == "Green") {
                    this.betbox.target = "green";
                    this.betbox.boxcolor = "rgb(76, 175, 80)";
                } else if (label == "Red") {
                    this.betbox.target = "red";
                    this.betbox.boxcolor = "rgb(244, 67, 54)";
                } else if (label == "Violet") {
                    this.betbox.target = "violet";
                    this.betbox.boxcolor = "rgb(156, 39, 176)";
                } else {
                    this.betbox.target = label;
                    this.betbox.boxcolor = "rgb(33, 150, 243)";
                }
                document.getElementById("betback").style.background = this.betbox.boxcolor;
                this.betbox.title = label;
                document.getElementById("betbox").style.display = "";
            }

        },
        up() {
            this.betbox.page = this.betbox.page + 1;
            this.fechresultrec();
        },
        down() {
            if (this.betbox.page > 1) {
                this.betbox.page = this.betbox.page - 1;
                this.fechresultrec();
            } else {
                this.pop("First Page Now!")
            }

        },
        up1() {
            if(this.betbox.total1/10>this.betbox.page1){
            this.betbox.page1 = this.betbox.page1 + 1;
            this.fechresultrec();  
            }
           
        },
        down1() {
            if (this.betbox.page1 > 1) {
                this.betbox.page1 = this.betbox.page1 - 1;
                this.fechresultrec();
            } else {
                this.pop("First Page Now!")
            }

        },
        closebetbox() {
            document.getElementById("betbox").style.display = "none";
            var listItems = document.getElementById('factorlist').childNodes;
            // Remove 'active' tag for all list items
            for (let i = 0; i < listItems.length; i++) {
                listItems[i].classList.remove('active');
            }
            this.betbox.amount = this.betbox.factor * this.betbox.multiply
            document.getElementById("10").className = "active";
            this.betbox.multiply = 1;
            this.betbox.factor = 10;
            this.betbox.amount = 10;
        },
        selectmultiply(multi) {
            var listItems = document.getElementById('factorlist').childNodes;
            // Remove 'active' tag for all list items
            for (let i = 0; i < listItems.length; i++) {
                listItems[i].classList.remove('active');
            }
            this.betbox.factor = multi;
            this.betbox.amount = this.betbox.factor * this.betbox.multiply
            document.getElementById(multi).className = "active";
        },
        plus() {
            this.betbox.multiply = this.betbox.multiply + 1;
            this.betbox.amount = this.betbox.factor * this.betbox.multiply
        },
        minus() {
            if (this.betbox.multiply > 1) {
                this.betbox.multiply = this.betbox.multiply - 1;
                this.betbox.amount = this.betbox.factor * this.betbox.multiply
            }

        },
        countdown() {
            var countDownDate = Date.parse(new Date) / 1e3;
            //var now = new Date().getTime();
            var distance = 180 - countDownDate % 180;
            //alert(distance);
            var i = distance / 60,
                n = distance % 60;
            //o = n / 10,
            // s = n % 10;
            var minutes = Math.floor(i);
            var seconds = ('0' + Math.floor(n)).slice(-2);
            var sec1 = (seconds % 100 - seconds % 10) / 10;
            var sec2 = seconds % 10;
            document.getElementById("demo").innerHTML = "<span  data-v-b74b9dc6='' class='span'>0</span><span  data-v-b74b9dc6=''class='span'>" + Math.floor(minutes) + "</span><span  data-v-b74b9dc6=''>:</span><p  data-v-b74b9dc6=''><span  data-v-b74b9dc6='' class='span'>" + sec1 + "</span><span  data-v-b74b9dc6='' class='span'>" + sec2 + "</span></p> ";
            document.getElementById("con").style.display = "none";
            console.log(distance);
            if (30 < distance) {
                var listItems = document.getElementById('btn3').childNodes;
                this.betbox.timeout = true;
                // Remove 'active' tag for all list items
                for (let i = 0; i < listItems.length; i++) {
                    listItems[i].classList.remove('btn_active');
                }
                var numitems = document.getElementById('btn10').getElementsByTagName('ol');
                for (let j = 0; j < numitems.length; j++) {
                    numitems[j].classList.remove('btn_active');
                }
            }
            if (30 > distance) {
                document.getElementById("betbox").style.display = "none";
                var listItems1 = document.getElementById('btn3').childNodes;
                this.betbox.timeout = false;
                // Remove 'active' tag for all list items
                for (let i = 0; i < listItems1.length; i++) {
                    listItems1[i].classList.add('btn_active');
                }
                var numitems1 = document.getElementById('btn10').getElementsByTagName('ol');
                for (let j = 0; j < numitems1.length; j++) {
                    numitems1[j].classList.add('btn_active');
                }
            }
            if (180 == distance) {
                window.location.reload();
            }
            if (170 == distance) {
                this.fechresultrec();
            }
            if (175 == distance) {
                this.fechresultrec();
            }
            if (179 == distance) {
                this.fechresultrec();
            }
            if (177 == distance) {
                this.fechresultrec();
            }

        },
        toggle(idx) {
            if(document.getElementById(idx).style.display==""){
                document.getElementById(idx).style.display="none";
            }else{
                document.getElementById(idx).style.display="";
            }
      },
        confirmbet() {
            document.getElementById("betbox").style.display = "none";
            if (this.userdetails.balance >= this.betbox.amount) {
                var data = new FormData();
                data.append("username", this.userdetails.username);
                data.append("period", this.userdetails.period);
                data.append("amount", this.betbox.amount);
                data.append("ans", this.betbox.target);
                axios
                    .post(
                        "https://demo.com/trova/src/api/bet.php?action=bet",
                        data
                    )
                    .then(res => {
                       console.log(res.data);
                        this.closebetbox();
                        this.loading();
                        this.pop("Success");
                        this.fechresultrec();        
                    })
                    .catch(err => {
                        console.log("Error", err);
                    });
            } else {
                this.pop("Your balance is insufficient");
            }

        },
        load(){
        this.loading();
        this.fechresultrec();
        },
        expand() {
            console.log("hi");
            var coll = document.getElementsByClassName("van-cell van-cell--clickable van-collapse-item__title");
            var i;
            for (i = 0; i < coll.length; i++) {

                coll[i].addEventListener("click", function () {
                    this.classList.toggle("active");
                    console.log("hii");
                    var content = this.nextSibling;
                    if (content.style.display === "block") {
                        content.style.display = "none";
                        console.log("close");
                    } else {
                        console.log("open");
                        content.style.display = "block";
                    }
                });
            }
        }

    }
}

</script>