<script>
import axios from "axios";
export default {
    name: "LoginView",
    data() {
        return {
            userdetails: {
                username: null,
                balance: null,
                usercode: null,
                commision: "0",
                intrest: "0",
                notice: "Welcome",

            },
            user: [],
            dailysign:[]
        }

    },
    beforeCreate: function () {
        if (localStorage.getItem('username') == null) {
            this.$router.push({ name: 'login' });
        }

    },
    created: function () {
        this.getUserdetails();


    },
    mounted: function () {
        this.loading();
    },

    methods: {
        pop(message) {
            document.getElementById("snackbar").innerHTML = message;
            document.getElementById("snackbar").style.display = "";
            setTimeout(function () { document.getElementById("snackbar").style.display = "none"; }, 1000);
        },
        islogout() {
            document.getElementById("logout").style.display = "";
        },
        loading() {
            document.getElementById("loading").style.display = "";
            setTimeout(function () { document.getElementById("loading").style.display = "none"; }, 200);
        },
        logout() {
            localStorage.removeItem('username')
            this.$router.push("/login");
        },
        logcancel() {
            document.getElementById("logout").style.display = "none";
        },
        shownotice() {
            document.getElementById("notice").style.display = "";
        },
        hidenotice() {
            document.getElementById("notice").style.display = "none";
        },
        sign(){
            document.getElementById("signbox").style.display = "";
        },
        signclose(){
            document.getElementById("signbox").style.display = "none";
        },
        show(id) {
            var x = document.getElementById(id);
            if (x.style.display === "none") {
                x.style.display = "block";
                console.log("open");
            } else {
                x.style.display = "none";
                console.log("close");

            }
        },
        getUserdetails() {

            this.userdetails.username = localStorage.getItem('username');
            axios.get('https://demo.com/trova/src/api/me.php?action=getuserinfo&user=' + this.userdetails.username).then((res) => {
                this.Users = res.data.user_Data;
                console.log(this.Users);
                this.userdetails.id = this.Users[0].id;
                this.userdetails.usercode = this.Users[0].usercode;
                this.userdetails.balance = this.Users[0].balance;
                this.userdetails.notice = this.Users[1].notice;
                this.userdetails.commision = this.Users[2].bonus;
                this.userdetails.days=this.Users[3].days;
                this.userdetails.status=this.Users[4].status;
                
            }).catch((err) => {
                console.log(err);
            })

            axios.get('https://demo.com/trova/src/api/bet.php?action=dailysign&username=' + this.userdetails.username).then((res) => {
                this.dailysign = res.data[0];
                console.log(this.dailysign);
                
            }).catch((err) => {
                console.log(err);
            })



        },
        signday(){
            if(this.dailysign.status=='Not signed in'){
                document.getElementById("signbox").style.display = "none";
                this.pop("Success");
                axios.get('https://demo.com/trova/src/api/bet.php?action=todaysign&username=' + this.userdetails.username)
            }else{
                this.pop("Had signed in");
            }
            

        }
    }

}
</script>