<template>
    <div data-v-51265586="" class="recharge">
        <nav data-v-51265586="" class="top_nav">
            <div data-v-51265586="" onclick="window.location.href='#/mine'" class="left"><img data-v-51265586=""
                    src="data:image/png;base64,"
                    alt=""><span data-v-51265586="">Withdrawal</span></div>
            <div data-v-51265586="" onclick="window.location.href='#/withdrawalrecord'" class="right"><img
                    data-v-51265586=""
                    src="data:image/png;base64,"
                    alt=""></div>
        </nav>
        <div data-v-51265586="" class="recharge_box">
            <p data-v-51265586="" class="top_text">Balance:  <span data-v-51265586="">{{ balance }}</span></p>
            <div data-v-51265586="" class="code_input_box">
                <div data-v-51265586="" class="code_input"><img data-v-51265586=""
                        src="data:image/png;base64,"
                        alt=""><input data-v-51265586="" v-model="withdraw" type="text"
                        placeholder="Enter withdrawal amount"></div>
            </div>
            <div data-v-51265586="" class="text_field">
                <p data-v-51265586="">Fee: <span data-v-51265586="">{{ 3 / 100 * withdraw }}</span>,to account <span
                        data-v-51265586="">{{ 97 / 100 * withdraw }}</span></p>
            </div>
            <div data-v-51265586="" class="payment_box">
                <p data-v-51265586="" class="payment_text">Payout</p>
                <div data-v-51265586="" role="radiogroup" class="van-radio-group">
                    <div data-v-51265586="" role="radio" tabindex="0" aria-checked="true" class="van-radio">
                        <div class="van-radio__icon van-radio__icon--square van-radio__icon--checked"><i
                                class="van-icon van-icon-success">
                                <!---->
                            </i></div><span class="van-radio__label"><span data-v-51265586=""
                                class="text">Bankcard</span></span>
                    </div>
                </div>
            </div>
            <div data-v-51265586="" class="add_card">
                <div data-v-51265586="" class="van-collapse van-hairline--top-bottom">
                    <div data-v-51265586="" class="van-collapse-item">
                        <div role="button" tabindex="0" aria-expanded="true" @click="openbank()"
                            class="van-cell van-cell--clickable van-collapse-item__title van-collapse-item__title--expanded">
                            <div class="van-cell__title"><img data-v-51265586=""
                                    src="data:image/png;base64,"
                                    alt="">
                                <div data-v-51265586="" class="nav_name">{{ promt }}</div>
                            </div><i class="van-icon van-icon-arrow van-cell__right-icon">
                                <!---->
                            </i>
                        </div>
                        <div class="van-collapse-item__wrapper" id="bankcard" style="display: none;">
                            <div class="van-collapse-item__content">
                                <div data-v-51265586="" id="card" style="display: none;" @click="selectcard()"
                                    class="nav_content">{{ bankcard.bankname }} {{ bankcard.account }}</div>

                                <div data-v-51265586="" onclick="window.location.href='#/addbankcard'"
                                    class="nav_content">Add Bank Card</div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div data-v-51265586="" class="password_box">
                <div data-v-51265586="" class="box_border"><img data-v-51265586=""
                        src="data:image/png;base64,"
                        alt=""><input data-v-51265586="" v-model="password" type="password"
                        placeholder="Enter your login password"></div>
            </div>
            <div data-v-51265586="" class="recharge_btn"><button data-v-51265586=""
                    @click="withdrawal()">Withdrawal</button></div>
        </div>
        <div data-v-405e9a63="" data-v-aa6d4f0a="" class="footer">
            <ul data-v-405e9a63="" class="nav_foot">
                <li data-v-405e9a63="" onclick="window.location.href='#/'" class=""><img data-v-405e9a63=""
                        src="data:image/png;base64,"
                        alt=""><span data-v-405e9a63="">Home</span></li>
                <li data-v-405e9a63="" onclick="window.location.href='#/Search'" class=""><img data-v-405e9a63=""
                        src="data:image/png;base64,"
                        alt=""><span data-v-405e9a63="">Search</span></li>
                <li data-v-405e9a63="" onclick="window.location.href='#/win'" class=""><img data-v-405e9a63=""
                        src="data:image/png;base64,"
                        alt=""><span data-v-405e9a63="">Win</span></li>
                <li data-v-405e9a63="" onclick="window.location.href='#/mine'" class="active"><img data-v-405e9a63=""
                        src="data:image/png;base64,"
                        alt=""><span data-v-405e9a63="">My</span></li>
            </ul>
        </div>
        <div id="snackbar" class="van-toast van-toast--middle van-toast--text" style="z-index: 2009;display:none ">
            <div class="van-toast__text">success</div>
        </div>
        <div data-v-74fec56a="" data-v-51265586="" id="loading" class="loading" style="display: none;">
            <div data-v-74fec56a="" class="v-dialog v-dialog--persistent" style="width: 300px; display: block;">
                <div data-v-74fec56a="" data-v-5197ff2a="" class="v-card v-sheet theme--dark teal">
                    <div data-v-74fec56a="" data-v-5197ff2a="" class="v-card__text"><span
                            data-v-74fec56a="">Loading</span>
                        <div data-v-74fec56a="" data-v-5197ff2a="" role="progressbar" aria-valuemin="0"
                            aria-valuemax="100" class="v-progress-linear mb-0" style="height: 7px;">
                            <div data-v-74fec56a="" class="v-progress-linear__background white"
                                style="height: 7px; opacity: 0.3; width: 100%;"></div>
                            <div data-v-74fec56a="" class="v-progress-linear__bar">
                                <div data-v-74fec56a=""
                                    class="v-progress-linear__bar__indeterminate v-progress-linear__bar__indeterminate--active">
                                    <div data-v-74fec56a="" class="v-progress-linear__bar__indeterminate long white">
                                    </div>
                                    <div data-v-74fec56a="" class="v-progress-linear__bar__indeterminate short white">
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import axios from "axios";
export default {
    name: "WithdrawalView",
    data() {
        return {
            username: null,
            balance: null,
            withdraw: null,
            password: null,
            cardselect: false,
            bankcard: [],
            result: [],
            promt: "Select Bank Card"
        }

    },
    mounted: function () {
        this.getUserdetails()
    },
    methods: {
        loading() {
            document.getElementById("loading").style.display = "";
            setTimeout(function () { document.getElementById("loading").style.display = "none"; }, 200);
        },
        pop(message) {
            document.getElementById("snackbar").innerHTML = message;
            document.getElementById("snackbar").style.display = "";
            setTimeout(function () { document.getElementById("snackbar").style.display = "none"; }, 3000);
        },
        getUserdetails() {

            this.username = localStorage.getItem('username');
            axios.get('https://demo.com/trova/src/api/me.php?action=getuserinfo&user=' + this.username).then((res) => {
                this.Users = res.data.user_Data;

                this.balance = this.Users[0].balance;

                console.log(this.Users);
            }).catch((err) => {
                console.log(err);
            })
            axios.get('https://demo.com/trova/src/api/bet.php?action=bankcard&user=' + this.username).then((res) => {
                this.bankcard = res.data[0];
                console.log(this.bankcard);
                if (this.bankcard.account == 1111111111) {
                    document.getElementById("card").style.display = "none";

                } else {

                    document.getElementById("card").style.display = "";
                }
            }).catch((err) => {
                console.log(err);
            })

        },
        withdrawal() {
            if (this.withdraw == null) {
                this.pop("Enter witdrawl amount");
            } else {
                if (this.cardselect == false) {
                    this.pop("Select Bank Card");
                } else {
                    if (this.password == null) {
                        this.pop("Enter your login password");
                    } else {
                        if (this.withdraw < 211) {
                            this.loading();
                            this.pop("Minimum withdraw 211");
                        } else {
                            if (this.password != this.bankcard.password) {
                                this.loading();
                                this.pop("Your password is incorrect");
                            } else {
                                if (this.withdraw * 1 > this.bankcard.balance) {
                                    this.loading();
                                    this.pop("Your balance is not enough");
                                    console.log(this.withdraw);
                                    console.log(this.withdraw < this.bankcard.balance);
                                } else {
                                    axios.get('https://demo.com/trova/src/api/bet.php?action=withdrawal&user=' + this.username + '&amount=' + this.withdraw).then((res) => {
                                        this.result = res.data[0];
                                        this.pop(this.result.status);
                                        if (this.result.status == "Success") {
                                            this.loading();
                                            this.getUserdetails();
                                            this.withdraw = null;
                                            setTimeout(() => { this.$router.push("/withdrawalrecord"); }, 600);
                                        }
                                        console.log(this.result);
                                    }).catch((err) => {
                                        console.log(err);
                                    })
                                }

                            }
                        }
                    }
                }
            }
        },

        openbank() {
            if (document.getElementById("bankcard").style.display == 'none') {
                document.getElementById("bankcard").style.display = "";
            } else {
                document.getElementById("bankcard").style.display = "none";
            }

        },
        selectcard() {
            if (this.cardselect == false) {
                document.getElementById("bankcard").style.display = "none";
                this.promt = this.bankcard.bankname + '' + this.bankcard.account;
                this.cardselect = true;
            } else {
                document.getElementById("bankcard").style.display = "none";
            }



        }
    }

}
</script>