                    commission. In this way, your team can spread quickly. Therefore, I hope everyone can use our
                    platform to make money, make money, and make money!Level 1 commission: Friends who join through your
                    own link belong to your level, when they trade, you will get 30% commission.Tier 2 commission:
                    Friends who join through your friend link belong to your secondary commission. When they trade, you
                    can get 20% commission.Level 3 commission: Friends who join through friends of friends belong to
                    your level 3. When they trade, you get 10% commission.Promotional rewards: 10% bonus amount for the
                    first recharge after the first-level lower level joins. If your friend joins through your invitation
                    and recharges 1000 for the first time, you will get 200</p>
