<script>
import axios from "axios";
export default {
    name: "PromotionView",
    data() {
        return {
            userdetails: {
                username: null,
                usercode: 0,
                balance: null,
                page: 1,
                level:1,
                total22: 0
            },
            invite:{

            },
            inviterecord: {

            },
            info: {

            }

        }

    },
    mounted: function prepare() {
        this.getrecord();
        this.rec();
    },
    methods: {
        pop(message) {
            document.getElementById("snackbar").innerHTML = message;
            document.getElementById("snackbar").style.display = "";
            setTimeout(function () { document.getElementById("snackbar").style.display = "none"; }, 1000);
        },
        level1(){
            document.getElementById("evel1").className="van-tab van-tab--active";
            document.getElementById("eve2").className="van-tab";
            document.getElementById("level3").className="van-tab";
            this.userdetails.level=1;
            this.rec();
        },
        level2(){
            document.getElementById("evel1").className="van-tab";
            document.getElementById("eve2").className="van-tab van-tab--active";
            document.getElementById("level3").className="van-tab";
            this.userdetails.level=2;
            this.rec();
        },
        level3(){ 
             document.getElementById("level3").className="van-tab van-tab--active";
            document.getElementById("evel1").className="van-tab";
            document.getElementById("eve2").className="van-tab";
            this.userdetails.level=3;
            this.rec();
          
        },
        closenotice() {
            document.getElementById("notice").style.display = "none";
        },
        getrecord() {
            this.userdetails.username = localStorage.getItem('username');
            
            axios.get('https://demo.com/trova/src/api/me.php?action=getuserinfo&user=' + this.userdetails.username).then((res) => {
                this.info = res.data.user_Data[0];
                console.log(this.info);
                
            }).catch((err) => {
                console.log(err);
            })
            
            axios.get('https://demo.com/trova/src/api/bet.php?action=invite&usercode='+ this.userdetails.username).then((res) => {
                this.invite = res.data[0];
                console.log(this.invite);
                if(this.invite.bonus==null){
                    this.invite.bonus=0.00;
                }
            }).catch((err) => {
                console.log(err);
            })
        },
        rec(){
            axios.get('https://demo.com/trova/src/api/bet.php?action=inviterecord&user=' + this.userdetails.username + '&page=' + this.userdetails.page+'&level=' + this.userdetails.level).then((res) => {
                this.inviterecord = res.data;
                console.log(this.inviterecord);
            }).catch((err) => {
                console.log(err);
            })
        },
        up() {
            this.userdetails.page = this.userdetails.page + 1;
            this.getrecord();
        },
        down() {
            if (this.userdetails.page == 1) {
                this.pop("First Page Now!")
            } else {
                this.userdetails.page = this.userdetails.page - 1;
                this.getrecord();
            }

        },
        copylink() {
            this.pop("Success");
            var inputc = document.body.appendChild(document.createElement("input"));
            inputc.value = document.getElementById("link").innerHTML;
            inputc.focus();
            inputc.select();
            document.execCommand('copy');
            inputc.parentNode.removeChild(inputc);
        },
        showlist(){
            document.getElementById("list").style.display = "";  
        },
        closelist(){
            document.getElementById("list").style.display = "none";  
        }
    }
}

</script>
