<template>
    <div data-v-b351b8b8="" class="addbankcard">
        <nav data-v-b351b8b8="" class="top_nav">
            <div data-v-b351b8b8="" onclick="window.location.href='#/bankcard'" class="left"><img data-v-b351b8b8=""
                    src="data:image/png;base64,"
                    alt=""><span data-v-b351b8b8="">Add Bank Card</span></div>
        </nav>
        <div data-v-b351b8b8="" class="input_card">
            <ul data-v-b351b8b8="" class="card_ul">
                <li data-v-b351b8b8="">
                    <p data-v-b351b8b8="">Actual Name</p><input data-v-b351b8b8="" v-model="userdetails.actualname"
                        type="text">
                </li>
                <li data-v-b351b8b8="">
                    <p data-v-b351b8b8="">IFSC Code</p><input data-v-b351b8b8="" v-model="userdetails.ifsc" type="text">
                </li>
                <li data-v-b351b8b8="">
                    <p data-v-b351b8b8="">Bank Name</p><input data-v-b351b8b8="" v-model="userdetails.bankname"
                        type="text">
                </li>
                <li data-v-b351b8b8="">
                    <p data-v-b351b8b8="">Upi Id</p><input data-v-b351b8b8="" v-model="userdetails.upi"
                        type="text">
                </li>
                <li data-v-b351b8b8="">
                    <p data-v-b351b8b8="">Bank Account</p><input data-v-b351b8b8="" v-model="userdetails.account"
                        type="text">
                </li>
                <li data-v-b351b8b8="">
                    <p data-v-b351b8b8="">State/Territory</p><input data-v-b351b8b8="" v-model="userdetails.state"
                        type="text">
                </li>
                <li data-v-b351b8b8="">
                    <p data-v-b351b8b8="">City</p><input data-v-b351b8b8="" v-model="userdetails.city" type="text">
                </li>
                <li data-v-b351b8b8="">
                    <p data-v-b351b8b8="">Address</p><input data-v-b351b8b8="" v-model="userdetails.address"
                        type="text">
                </li>
                <li data-v-b351b8b8="">
                    <p data-v-b351b8b8="">Mobile Number</p><input data-v-b351b8b8="" v-model="userdetails.mobile"
                        type="text">
                </li>
                <li data-v-b351b8b8="">
                    <p data-v-b351b8b8="">Email</p><input data-v-b351b8b8="" v-model="userdetails.email" type="text">
                </li>
                <li data-v-b351b8b8="">
                    <p data-v-b351b8b8="">Account phone number</p><input data-v-b351b8b8="" type="text"
                        v-model="userdetails.actualnumber" disabled="true">
                </li>
                <li data-v-b351b8b8="">
                    <p data-v-b351b8b8="">Code</p>
                    <div data-v-b351b8b8="" style="display: flex;"><input data-v-b351b8b8="" v-model="userdetails.code" type="text"
                            placeholder="Verification Code" style="flex: 1 1 0%;"><span data-v-b351b8b8=""
                            class="tips_span">Verification Code is required</span><button @click="getotp()" data-v-b351b8b8=""
                            class="gocode">
                            OTP
                        </button></div>
                </li>
            </ul>
            <div data-v-b351b8b8="" class="continue_btn"><button data-v-b351b8b8="" @click="addbankcard()" class="ripple">Continue</button>
            </div>
        </div>
        <div id="snackbar" class="van-toast van-toast--middle van-toast--text" style="z-index: 2009;display:none ">
            <div class="van-toast__text">success</div>
        </div>
        <div data-v-405e9a63="" data-v-aa6d4f0a="" class="footer">
            <ul data-v-405e9a63="" class="nav_foot">
                <li data-v-405e9a63="" onclick="window.location.href='#/'" class=""><img data-v-405e9a63=""
                        src="data:image/png;base64,"
                        alt=""><span data-v-405e9a63="">Home</span></li>
                <li data-v-405e9a63="" onclick="window.location.href='#/Search'" class=""><img data-v-405e9a63=""
                        src="data:image/png;base64,"
                        alt=""><span data-v-405e9a63="">Search</span></li>
                <li data-v-405e9a63="" onclick="window.location.href='#/win'" class=""><img data-v-405e9a63=""
                        src="data:image/png;base64,"
                        alt=""><span data-v-405e9a63="">Win</span></li>
                <li data-v-405e9a63="" onclick="window.location.href='#/mine'" class="active"><img data-v-405e9a63=""
                        src="data:image/png;base64,"
                        alt=""><span data-v-405e9a63="">My</span></li>
            </ul>
        </div>
    </div>
</template>


<script>


import axios from "axios";
export default {
    name: "RechargeRecord",
    data() {
        return {
            userdetails: {
                username: null,
                id: null,
                actualname: null,
                ifsc: null,
                bankname: null,
                account: null,
                state: null,
                city: null,
                upi: null,
                address: null,
                mobile: null,
                email: null,
                code: null,
                actualnumber: null,
            },
            bankcard: {

            },
            result: {

            }

        }

    },
    mounted: function () {
        this.prepare();
    },
    methods: {
        pop(message) {
            document.getElementById("snackbar").innerHTML = message;
            document.getElementById("snackbar").style.display = "";
            setTimeout(function () { document.getElementById("snackbar").style.display = "none"; }, 1000);
        },
        prepare() {
            this.userdetails.actualnumber = 91 + localStorage.getItem('username');
            this.userdetails.username = localStorage.getItem('username');
            axios.get('https://demo.com/trova/src/api/bet.php?action=bankcard&user=' + this.userdetails.username).then((res) => {
                this.bankcard = res.data[0];
                console.log(this.bankcard);
                this.userdetails.id = this.$route.query.id;
                if (this.userdetails.id == this.bankcard.id) {
                    console.log(this.$route.query.id);
                    this.userdetails.actualname = this.bankcard.name;
                    this.userdetails.ifsc = this.bankcard.ifsc;
                    this.userdetails.bankname = this.bankcard.bankname;
                    this.userdetails.account = this.bankcard.account;
                    this.userdetails.state = this.bankcard.state;
                    this.userdetails.city = this.bankcard.city;
                    this.userdetails.upi= this.bankcard.upi;
                    this.userdetails.address = this.bankcard.address;
                    this.userdetails.mobile = this.bankcard.mobile;
                    this.userdetails.email = this.bankcard.email;
                }
            }).catch((err) => {
                console.log(err);
            })
        },
        addbankcard() {
            this.userdetails.username = localStorage.getItem('username');
            var data = new FormData();
            data.append("actualname", this.userdetails.actualname),
            data.append("ifsc", this.userdetails.ifsc),
            data.append("bankname", this.userdetails.bankname),
            data.append("account", this.userdetails.account),
            data.append("upi", this.userdetails.upi),
            data.append("state", this.userdetails.state),
            data.append("city", this.userdetails.city),
            data.append("address", this.userdetails.address),
            data.append("mobile", this.userdetails.mobile),
            data.append("email", this.userdetails.email),
            data.append("code", this.userdetails.code),
            axios
                .post(
                    "https://demo.com/trova/src/api/bet.php?action=addbankcard&user=" + this.userdetails.username,
                    data
                ).then((res) => {
                    this.result = res.data[0];
                    console.log(this.result.status);
                   console.log(this.userdetails)
                    if(this.result.status=='success'){
                        this.$router.push('/bankcard');
                    }else{
                        this.pop(this.result.status);
                    }
                }).catch((err) => {
                    console.log(err);
                })
        },
        getotp(){
            axios.get('https://demo.com/trova/src/api/otp.php?num=' + this.userdetails.username);
            this.pop("otp sent");
        }

    }
}

</script>