<template>
    <div data-v-2a6a9292="" class="recharge">
        <nav data-v-2a6a9292="" class="top_nav">
            <div data-v-2a6a9292="" class="left" onclick="window.location.href='#/mine'" ><img data-v-2a6a9292=""
                    src="data:image/png;base64,"
                    alt=""><span data-v-2a6a9292="">Transactions</span></div>
        </nav>
        <div data-v-2a6a9292="" class="recharge_box">
            <div data-v-2a6a9292="" class="completed_list">
                <ul data-v-2a6a9292="" class="list_box">
                    <li data-v-2a6a9292="" v-for="row in rechargerecord" :key="row.id">
                        <ol data-v-2a6a9292="">
                            <p data-v-2a6a9292=""> {{row.amount}}</p>
                       
                        </ol>
                        <ol data-v-2a6a9292="">
                            <p data-v-2a6a9292="">Place Order</p>
                            <p data-v-2a6a9292="" class="times">{{row.res}}</p>
                        </ol>
                        <ol data-v-2a6a9292="">
                            <p data-v-2a6a9292="" class="oddnum">{{row.time}}</p>
                        </ol>
                    </li>
                    
                </ul>
            </div><!---->
            <div data-v-2a6a9292="" class="pagination">
                <ul data-v-2a6a9292="" class="page_box">
                    <li data-v-2a6a9292="" class="page"><span data-v-2a6a9292="">{{(userdetails.page-1)*10}}-{{(userdetails.page)*10}}</span> of {{userdetails.total22}}
                    </li>
                    <li data-v-2a6a9292="" class="page_btn"><i data-v-2a6a9292=""
                            class="van-icon van-icon-arrow-left"  @click="down()"><!----></i><i data-v-2a6a9292=""
                            class="van-icon van-icon-arrow"  @click="up()"><!----></i></li>
                </ul>
            </div>
           
        </div>
        <div id="snackbar" class="van-toast van-toast--middle van-toast--text" style="z-index: 2009;display:none ">
            <div class="van-toast__text">success</div>
        </div>
        <div data-v-405e9a63="" data-v-2a6a9292="" class="footer">
            <ul data-v-405e9a63="" class="nav_foot">
                <li data-v-405e9a63="" onclick="window.location.href='#/'" class=""><img data-v-405e9a63=""
                        src="data:image/png;base64,"
                        alt=""><span data-v-405e9a63="">Home</span></li>
                <li data-v-405e9a63="" onclick="window.location.href='#/search'" class=""><img data-v-405e9a63=""
                        src="data:image/png;base64,"
                        alt=""><span data-v-405e9a63="">Search</span></li>
                <li data-v-405e9a63="" onclick="window.location.href='#/win'" class=""><img data-v-405e9a63=""
                        src="data:image/png;base64,"
                        alt=""><span data-v-405e9a63="">Win</span></li>
                <li data-v-405e9a63="" onclick="window.location.href='#/mine'" class="active"><img data-v-405e9a63=""
                        src="data:image/png;base64,"
                        alt=""><span data-v-405e9a63="">My</span></li>
            </ul>
        </div>
        <div data-v-74fec56a="" data-v-2a6a9292="" class="loading" style="display: none;">
            <div data-v-74fec56a="" class="v-dialog v-dialog--persistent" style="width: 300px; display: block;">
                <div data-v-74fec56a="" data-v-5197ff2a="" class="v-card v-sheet theme--dark teal">
                    <div data-v-74fec56a="" data-v-5197ff2a="" class="v-card__text"><span
                            data-v-74fec56a="">Loading</span>
                        <div data-v-74fec56a="" data-v-5197ff2a="" role="progressbar" aria-valuemin="0"
                            aria-valuemax="100" class="v-progress-linear mb-0" style="height: 7px;">
                            <div data-v-74fec56a="" class="v-progress-linear__background white"
                                style="height: 7px; opacity: 0.3; width: 100%;"></div>
                            <div data-v-74fec56a="" class="v-progress-linear__bar">
                                <div data-v-74fec56a=""
                                    class="v-progress-linear__bar__indeterminate v-progress-linear__bar__indeterminate--active">
                                    <div data-v-74fec56a="" class="v-progress-linear__bar__indeterminate long white">
                                    </div>
                                    <div data-v-74fec56a="" class="v-progress-linear__bar__indeterminate short white">
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>


<script>
import axios from "axios";
export default {
    name: "RechargeRecord",
    data() {
        return {
            userdetails: {
                username: null,
                balance: null,
                period: null,
                page: 1,
                total22:0
            },
            rechargerecord: {

            },
            info:{

            }

        }

    },
    mounted: function prepare() {
        this.getrecord();
    },
    methods: {
        pop(message) {
            document.getElementById("snackbar").innerHTML = message;
            document.getElementById("snackbar").style.display = "";
            setTimeout(function () { document.getElementById("snackbar").style.display = "none"; }, 1000);
        },
        getrecord() {
            this.userdetails.username = localStorage.getItem('username');
            axios.get('https://demo.com/trova/src/api/bet.php?action=trans&user=' + this.userdetails.username + '&page1=' + this.userdetails.page).then((res) => {
                this.rechargerecord = res.data;
                console.log(this.rechargerecord);
            }).catch((err) => {
                console.log(err);
            })
            axios.get('https://demo.com/trova/src/api/bet.php?action=info&user=' + this.userdetails.username).then((res) => {
                this.info = res.data;
                this.userdetails.total22 = this.info[4].trans;
                if (this.userdetails.total22 > 0) {
                    document.getElementById("norecord").style.display = "none";
                }
                console.log(this.info);
            }).catch((err) => {
                console.log(err);
            })
        },
        up(){
            this.userdetails.page = this.userdetails.page + 1;
            this.getrecord();
        },
        down(){
            if(this.userdetails.page==1){
                this.pop("First Page Now!")
            }else{
               this.userdetails.page = this.userdetails.page - 1;
               this.getrecord();  
            }
             
        }
    }
}

</script>