<template>
    <div data-v-be68bb7c="" class="bankcard">
        <nav data-v-be68bb7c="" class="top_nav">
            <div data-v-be68bb7c="" onclick="window.location.href='#/mine'" class="left"><img data-v-be68bb7c=""
                    src="data:image/png;base64,"
                    alt=""><span data-v-be68bb7c="">Bank Card</span></div>
            <div data-v-be68bb7c="" onclick="window.location.href='#/addbankcard'" class="right"><img data-v-be68bb7c=""
                    src="data:image/png;base64,"
                    alt=""></div>
        </nav>
        <div data-v-be68bb7c="" id="content" class="address_list">
            <ul data-v-be68bb7c="" id="card" class="list_ul">
                <li data-v-be68bb7c="">
                    <ol data-v-be68bb7c="" class="left_icon"><img data-v-be68bb7c=""
                            src="data:image/png;base64,"
                            alt=""></ol>
                    <ol data-v-be68bb7c="" class="center">
                        <p data-v-be68bb7c="" class="name">{{bankcard.name}}</p>
                        <p data-v-be68bb7c="" class="info_text">{{bankcard.account}}</p>
                    </ol>
                    <ol data-v-be68bb7c="" class="right_icon" @click="options()"><img data-v-be68bb7c=""
                            src="data:image/png;base64,"
                            alt=""></ol>
                </li>
            </ul>
        </div>
        <div data-v-405e9a63="" data-v-aa6d4f0a="" class="footer">
            <ul data-v-405e9a63="" class="nav_foot">
                <li data-v-405e9a63="" onclick="window.location.href='#/'" class=""><img data-v-405e9a63=""
                        src="data:image/png;base64,"
                        alt=""><span data-v-405e9a63="">Home</span></li>
                <li data-v-405e9a63="" onclick="window.location.href='#/Search'" class=""><img data-v-405e9a63=""
                        src="data:image/png;base64,"
                        alt=""><span data-v-405e9a63="">Search</span></li>
                <li data-v-405e9a63="" onclick="window.location.href='#/win'" class=""><img data-v-405e9a63=""
                        src="data:image/png;base64,"
                        alt=""><span data-v-405e9a63="">Win</span></li>
                <li data-v-405e9a63="" onclick="window.location.href='#/mine'" class="active"><img data-v-405e9a63=""
                        src="data:image/png;base64,"
                        alt=""><span data-v-405e9a63="">My</span></li>
            </ul>
        </div>
        <div id="over" class="van-overlay" @click="closeoptions()" style="z-index: 2001; display: none;"></div>
        
        <div data-v-be68bb7c="" id="options" class="van-popup van-popup--bottom" style="z-index: 2002; display: none;">
            <div data-v-be68bb7c="" class="popup_box">
                <p data-v-be68bb7c="" class="popup_title">Select</p>
                <ul data-v-be68bb7c="" class="popup_ul">
                    <li data-v-be68bb7c="" @click="edit()" ><img data-v-be68bb7c="" 
                            src="data:image/png;base64,"
                            alt=""><span data-v-be68bb7c="">Edit</span></li>
                    <li data-v-be68bb7c="" @click="opendelete()" ><img data-v-be68bb7c=""
                            src="data:image/png;base64,"
                            alt=""><span data-v-be68bb7c="">Delete</span></li>
                </ul>
            </div>
        </div>
        <div data-v-74fec56a="" data-v-be68bb7c="" id="load" class="loading" style="display: none;">
            <div data-v-74fec56a="" class="v-dialog v-dialog--persistent" style="width: 300px; display: block;">
                <div data-v-74fec56a="" data-v-5197ff2a="" class="v-card v-sheet theme--dark teal">
                    <div data-v-74fec56a="" data-v-5197ff2a="" class="v-card__text"><span
                            data-v-74fec56a="">Loading</span>
                        <div data-v-74fec56a="" data-v-5197ff2a="" role="progressbar" aria-valuemin="0"
                            aria-valuemax="100" class="v-progress-linear mb-0" style="height: 7px;">
                            <div data-v-74fec56a="" class="v-progress-linear__background white"
                                style="height: 7px; opacity: 0.3; width: 100%;"></div>
                            <div data-v-74fec56a="" class="v-progress-linear__bar">
                                <div data-v-74fec56a=""
                                    class="v-progress-linear__bar__indeterminate v-progress-linear__bar__indeterminate--active">
                                    <div data-v-74fec56a="" class="v-progress-linear__bar__indeterminate long white">
                                    </div>
                                    <div data-v-74fec56a="" class="v-progress-linear__bar__indeterminate short white">
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div data-v-be68bb7c="" id="delete"  class="delete_zz" style="display: none;,z-index: 2002;">
            <div data-v-be68bb7c="" class="warpper">
                <p data-v-be68bb7c="" class="title">Confirm</p>
                <p data-v-be68bb7c="" class="text">Do you want to delete this bank card? <span data-v-be68bb7c="">
                    {{bankcard.name}},{{bankcard.account}}</span></p>
                <div data-v-be68bb7c="" class="bot_btn"><button @click="closedelete()"
                        data-v-be68bb7c="">CANCEL</button><button data-v-be68bb7c="" @click="deletecard()" class="btn">DELETE</button></div>
            </div>
        </div>
    </div>
</template>

<script>
import axios from "axios";
export default {
    name: "RechargeRecord",
    data() {
        return {
            userdetails: {
                username: null,
                balance: null,
                period: null,
                page: 1,
            },
            bankcard: {

            },
            info: {

            }

        }

    },
    mounted: function (){
        this.prepare();
    },
    methods: {
        loading() {
            document.getElementById("load").style.display = "";
            document.getElementById("content").style.display = "none";

            setTimeout(function () {
                document.getElementById("load").style.display = "none";
                document.getElementById("content").style.display = "";
            }, 300);
        },
        getrecord() {
            this.userdetails.username = localStorage.getItem('username');
            axios.get('https://demo.com/trova/src/api/bet.php?action=bankcard&user=' + this.userdetails.username).then((res) => {
                this.bankcard = res.data[0];
                console.log(this.bankcard);
                //console.log(this.$route.query.test)
                if(this.bankcard.account==1111111111){
                    document.getElementById("card").style.display = "none";  
                }
            }).catch((err) => {
                console.log(err);
            })
        },
        deletecard(){
            this.userdetails.username = localStorage.getItem('username');
            axios.get('https://demo.com/trova/src/api/bet.php?action=deletebankcard&user=' + this.userdetails.username);
            this.closedelete();
            this.prepare();
            this.getrecord();
        },
        options() {
            document.getElementById("options").style.display = "";
            document.getElementById("over").style.display = "";
        },
        closeoptions() {
            document.getElementById("options").style.display = "none";
            document.getElementById("over").style.display = "none";
        },
        opendelete(){
            this.closeoptions();
            document.getElementById("delete").style.display = "";
        },
        closedelete(){ 
            document.getElementById("delete").style.display = "none";  
        },
        prepare() {
        this.getrecord();
        this.loading();
        },
        edit(){
            this.$router.push('/addbankcard?id='+this.bankcard.id);
        }

    }
}

</script>