    </div>
</template>

<script>
import axios from "axios";
export default {
    name: "RechargeView",
    data() {
        return {
            username:null,
            balance: null,
        }

    },
    mounted: function () {
        this.getUserdetails()
    },
    methods: {
        amount(eid, val) {
            var listItems = document.getElementById('paybox').childNodes;

            // Remove 'active' tag for all list items
            for (let i = 0; i < listItems.length; i++) {
                listItems[i].classList.remove("choose_active");
            }
            console.log(eid);
            // Add 'active' tag for currently selected item
            document.getElementById(eid).className = "choose_active";
            document.getElementById("amount").value = val;
        }
        ,
        getUserdetails() {

            this.username = localStorage.getItem('username');
            axios.get('https://demo.com/trova/src/api/me.php?action=getuserinfo&user=' + this.username).then((res) => {
                this.Users = res.data.user_Data;

                this.balance = this.Users[0].balance;

                console.log(this.Users);
            }).catch((err) => {
                console.log(err);
            })
        },
        proceed() {


            if (document.getElementById("amount").value > 199) {
                var link = "https://demo.com/trova/src/api/pay.php?user="+this.username+"&am="
                var amount = document.getElementById("amount").value;
                var payurl = link.concat(amount);
                window.open(payurl, "_blank");
            }
            else {
                var x = document.getElementById("snackbar");
                x.style.display = "";
                setTimeout(function e() {  x.style.display="none"; }, 1000);

            }
        }
    }

}
</script>