<template>
    <div data-v-309ccc10="" class="recharge">
        <nav data-v-309ccc10="" class="top_nav">
            <div data-v-309ccc10="" onclick="window.location.href='#/'" class="left"><img data-v-309ccc10=""
                    src="data:image/png;base64,"
                    alt=""><span data-v-309ccc10="">Login</span></div>
        </nav>
        <div data-v-309ccc10="" class="recharge_box">
            <form action="" method="POST">
                <div data-v-309ccc10="" class="input_box"><img data-v-309ccc10=""
                        src="data:image/png;base64,"
                        alt="" id="numunactive"><img data-v-309ccc10=""
                        src="data:image/png;base64,"
                        alt="" id="numactive" style="display: none;"><img data-v-309ccc10=""
                        src="data:image/png;base64,"
                        alt="" style="display: none;"><input data-v-309ccc10="" id="username" @focus="num()"
                        v-model="user.username" type="text" style="outline:none;" placeholder="Mobile Number"><span
                        data-v-309ccc10="" class="tips_span">Mobile number is
                        required</span></div>
                <div data-v-309ccc10="" class="input_box"><img data-v-309ccc10=""
                        src="data:image/png;base64,"
                        alt="" id="passunactive"><img data-v-309ccc10=""
                        src="data:image/png;base64,"
                        alt="" id="passactive" style="display: none;"><img data-v-309ccc10=""
                        src="data:image/png;base64,"
                        alt="" id="passerror" style="display: none;"><input data-v-309ccc10="" style="outline:none;"
                        v-model="user.password" type="password" @focus="pass()" @focusout="passout()"
                        placeholder="Password"><span data-v-309ccc10="" class="tips_span" id="passerrortxt"
                        style="display: none; bottom: -25px; z-index: 2;">Password is required</span></div>
                <div data-v-309ccc10="" class="input_box_btn"><button data-v-309ccc10="" @click.prevent="onLogin()"
                        class="login_btn ripple">Login</button></div>
                <div data-v-309ccc10="" class="input_box_btn">
                    <div data-v-309ccc10="" class="two_btn"><button data-v-309ccc10="" onclick="window.location.href='#/register'"
                            class="ripplegrey">Register</button><button data-v-309ccc10="" onclick="window.location.href='#/forgotpass'" class="ripplegrey">Forgot
                            Password?</button></div>
                </div>
            </form>
        </div>
        <div data-v-405e9a63="" data-v-309ccc10="" class="footer">
            <ul data-v-405e9a63="" class="nav_foot">
                <li data-v-405e9a63="" onclick="window.location.href='#/'" class=""><img data-v-405e9a63=""
                        src="data:image/png;base64,"
                        alt=""><span data-v-405e9a63="">Home</span></li>
                <li data-v-405e9a63="" onclick="window.location.href='#/search'" class=""><img data-v-405e9a63=""
                        src="data:image/png;base64,"
                        alt=""><span data-v-405e9a63="">Search</span></li>
                <li data-v-405e9a63="" class="" style="display: none;"><img data-v-405e9a63=""
                        src="data:image/png;base64,"
                        alt=""><span data-v-405e9a63="">Win</span></li>
                <li data-v-405e9a63="" class="active"><img data-v-405e9a63=""
                        src="data:image/png;base64,"
                        alt=""><span data-v-405e9a63="">My</span></li>
            </ul>
        </div>
        <div id="snackbar" class="van-toast van-toast--middle van-toast--text" style="z-index: 2009;display:none ">
            <div class="van-toast__text">success</div>
        </div>
        <div data-v-74fec56a="" data-v-309ccc10="" id="loading" class="loading" style="display: none;">
            <div data-v-74fec56a="" class="v-dialog v-dialog--persistent" style="width: 300px; display: block;">
                <div data-v-74fec56a="" data-v-5197ff2a="" class="v-card v-sheet theme--dark teal">
                    <div data-v-74fec56a="" data-v-5197ff2a="" class="v-card__text"><span
                            data-v-74fec56a="">Loading</span>
                        <div data-v-74fec56a="" data-v-5197ff2a="" role="progressbar" aria-valuemin="0"
                            aria-valuemax="100" class="v-progress-linear mb-0" style="height: 7px;">
                            <div data-v-74fec56a="" class="v-progress-linear__background white"
                                style="height: 7px; opacity: 0.3; width: 100%;"></div>
                            <div data-v-74fec56a="" class="v-progress-linear__bar">
                                <div data-v-74fec56a=""
                                    class="v-progress-linear__bar__indeterminate v-progress-linear__bar__indeterminate--active">
                                    <div data-v-74fec56a="" class="v-progress-linear__bar__indeterminate long white">
                                    </div>
                                    <div data-v-74fec56a="" class="v-progress-linear__bar__indeterminate short white">
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>

</template>
<script>

import axios from "axios";
export default {
    name: "LoginView",
    data() {
        return {
            user: {
                username: null,
                password: null
            }
        }

    },
    beforeCreate: function () {
        if (localStorage.getItem('username') != null)
            this.$router.push({ name: 'mine' });
    },

    methods: {
        pop(message) {
            document.getElementById("snackbar").innerHTML = message;
            document.getElementById("snackbar").style.display = "";
            setTimeout(function () { document.getElementById("snackbar").style.display = "none"; }, 3000);
        },
        loading() {
            document.getElementById("loading").style.display = "";
            setTimeout(function () { document.getElementById("loading").style.display = "none"; }, 500);
        },
        onLogin() {
            if (this.user.username == null || this.user.password == null) {
                if (this.user.username == null) {
                    this.pop("Mobile number is requied")
                } else {
                    this.pop("Password is requied")
                }

            } else {
                var data = new FormData();
                data.append("username", this.user.username);
                data.append("password", this.user.password);
                axios
                    .post(
                        "https://demo.com/trova/src/api/userapi.php?action=login",
                        data
                    )
                    .then(res => {
                        if (res.data.error) {
                            this.loading();
                            console.log("Error", res.data);
                            this.pop(res.data.message);
                        } else {
                            this.loading();
                            localStorage.setItem('username', this.user.username.substring(3));
                            console.log("Success", res.data.message);
                            setTimeout(() => { this.$router.push("/mine"); }, 600);

                        }
                    })
                    .catch(err => {
                        console.log("Error", err);
                    });
            }
        },
        redirect() {
            console.log(this.$store.state.name);
            //this.$router.push({ name: 'Search' });
        },
        num() {
            document.getElementById("numactive").style.display = "";
            document.getElementById("numunactive").style.display = "none";
            if (document.getElementById("username").value == "") {
                document.getElementById("username").value = "+91";
            }
        },
        pass() {
            document.getElementById("numactive").style.display = "none";
            document.getElementById("numunactive").style.display = "";
            document.getElementById("passactive").style.display = "";
            document.getElementById("passerror").style.display = "none";
            document.getElementById("passerrortxt").style.display = "none";
            document.getElementById("passunactive").style.display = "none";
        },
        passout() {
            if (this.user.password.length == 0) {
                document.getElementById("passactive").style.display = "none";
                document.getElementById("passunactive").style.display = "none";
                document.getElementById("passerror").style.display = "";
                document.getElementById("passerrortxt").style.display = "";
            } else {
                document.getElementById("passactive").style.display = "none";
                document.getElementById("passunactive").style.display = "";
            }
        }

    }




}
</script>
