import axios from "axios";
export default {
    name: "RegisterView",
    data() {
        return {
            user: {
                username: null,
                password: null,
                code: null,
                refcode:null,
                agreement: true,
            }
        }

    },
    beforeCreate: function () {
        if (localStorage.getItem('username') != null)
            this.$router.push({ name: 'mine' });
    },
    created: function(){
        this.user.refcode=this.$route.query.r_code;
    },

    methods: {
        pop(message) {
            document.getElementById("snackbar").innerHTML = message;
            document.getElementById("snackbar").style.display = "";
            setTimeout(function () { document.getElementById("snackbar").style.display = "none"; }, 3000);
        },
        loading() {
            document.getElementById("loading").style.display = "";
            setTimeout(function () { document.getElementById("loading").style.display = "none"; }, 500);
        },
        onRegister() {
            if (this.user.username == null || this.user.password == null || this.user.code == null || this.user.refcode == null || this.user.agreement == false) {
                if (this.user.username == null) {
                    this.pop("Mobile number is requied")
                } else if (this.user.password == null) {
                    this.pop("Password is requied")
                } else if (this.user.code == null) {
                    this.pop("Verfication Code is requied")
                } else if (this.user.refcode == null) {
                    this.loading();
                    this.pop("Invalid parameters")
                } else {
                    this.pop("Please check I agree privacy policy")
                }

            } else {
                var data = new FormData();
                data.append("username", this.user.username.substring(3));
                data.append("password", this.user.password);
                data.append("code", this.user.code);
                data.append("refcode", this.user.refcode);
                axios
                    .post(
                        "https://demo.com/trova/src/api/bet.php?action=register",
                        data
                    )
                    .then(res => {
                        if (res.data[0].status=="success"){
                            this.loading();
                            this.pop(res.data[0].status);
                            console.log("Success", res.data[0].status);
                            setTimeout(() => { this.$router.push("/login"); }, 600);
                        } else {
                            this.loading();
                            console.log("Error", res.data);
                            this.pop(res.data[0].status);   
                        }
                    })
                    .catch(err => {
                        console.log("Error", err);
                    });
            }
        },
        redirect() {
            console.log(this.$store.state.name);
            //this.$router.push({ name: 'Search' });
        },
        otp() {
            if (this.user.username != null) {
                document.getElementById("otpbtn").className = "gocode_time";
                axios.get('https://demo.com/trova/src/api/otp.php?num=' + this.user.username.substring(3));
                this.pop("Success");
                this.timer(180);
            } else {
                this.pop("Mobile number is requied")
            }

        }, 
        timer(remaining) {
            document.getElementById('otpbtn').innerHTML = 'WAIT' + remaining + 's';
            remaining -= 1;

            if (remaining >= 0 ) {
                setTimeout(function () {
                    this.timer(remaining);
                }, 1000);
                return;
            }
            document.getElementById('otpbtn').innerHTML = "RESEND"
        },
        num() {
            document.getElementById("numactive").style.display = "";
            document.getElementById("numunactive").style.display = "none";
            if (document.getElementById("username").value == "") {
                document.getElementById("username").value = "+91";
            }
        },
        numout() {
            document.getElementById("numactive").style.display = "none";
            document.getElementById("numunactive").style.display = "";
        },
        pass() {
            document.getElementById("passactive").style.display = "";
            document.getElementById("passerror").style.display = "none";
            document.getElementById("passerrortxt").style.display = "none";
            document.getElementById("passunactive").style.display = "none";
        },
        passout() {
            if (this.user.password == null) {
                document.getElementById("passactive").style.display = "none";
                document.getElementById("passunactive").style.display = "none";
                document.getElementById("passerror").style.display = "";
                document.getElementById("passerrortxt").style.display = "";
            } else {
                document.getElementById("passactive").style.display = "none";
                document.getElementById("passunactive").style.display = "";
            }
        },
        code() {
            document.getElementById("codeactive").style.display = "";
            document.getElementById("codeerror").style.display = "none";
            document.getElementById("codeerrortxt").style.display = "none";
            document.getElementById("codeunactive").style.display = "none";
        },
        codeout() {
            if (this.user.code == null) {
                document.getElementById("codeactive").style.display = "none";
                document.getElementById("codeunactive").style.display = "none";
                document.getElementById("codeerror").style.display = "";
                document.getElementById("codeerrortxt").style.display = "";
            } else {
                document.getElementById("codeactive").style.display = "none";
                document.getElementById("codeunactive").style.display = "";
            }
        },
        ref() {
            document.getElementById("refactive").style.display = "";
            document.getElementById("refunactive").style.display = "none";
        },
        refout() {
            document.getElementById("refactive").style.display = "none";
            document.getElementById("refunactive").style.display = "";
        },
        policy() {
            document.getElementById("policy").style.display = "";
        },
        policyclose() {
            document.getElementById("policy").style.display = "none";
        },
        tick() {
            if (this.user.agreement) {
                document.getElementById("tick2").className = "van-checkbox__icon van-checkbox__icon--square";
                document.getElementById("tickfalse").style.display = "";
                document.getElementById("ticktrue").style.display = "none";
                this.user.agreement = false;
            } else {
                document.getElementById("tick2").className = "van-checkbox__icon van-checkbox__icon--square van-checkbox__icon--checked";
                this.user.agreement = true;
                document.getElementById("ticktrue").style.display = "";
                document.getElementById("tickfalse").style.display = "none";
            }
        }

    }




}
</script>